<template>
  <div>
    <b-container class="top">
        <h1 class="main-title">Lista de presentes</h1>
        <b-row>
          <b-col class="block" md="4" sm="12">
            <b-card @click="showModal('modalHomeHelps')"
                img-src="/static/imgs/cooking.svg"
                img-alt="Lulu"
                img-center>
              <p class="card-text text-center">
                Ajudinha na Cozinha
              </p>
              </b-card>
          </b-col>
          <b-col class="block" md="4" sm="12">
            <b-card
                img-src="/static/imgs/pin.svg"
                img-alt="Lulu"
                img-center>
              <p class="card-text text-center">
                Ajudinha na casa
              </p>
              </b-card>
          </b-col>

          <b-col class="block" md="4" sm="12">
            <b-card
                img-src="/static/imgs/honeymoon.svg"
                img-alt="Lulu"
                img-center>
              <p class="card-text text-center">
                Ajudinha na lua-de-mel
              </p>
              </b-card>
          </b-col>

        </b-row>


        <b-modal id="modalHomeHelps" title="Ajudinha na Cozinha" cancel-title="Fechar" @ok="uncheck" centered  ref="modalHomeHelps" size="lg" >
          <b-container>
            <b-row>
              <b-col cols="12">
                  <img class="img" src="/static/imgs/jogo_jantar.jpg" />
                  <p class="item">Jogo de jantar</p>
                  <p class="price">R$ 200,00</p>
                  <form target="paypal" action="https://www.paypal.com/cgi-bin/webscr" method="post" >
                    <input type="hidden" name="cmd" value="_s-xclick">
                    <input type="hidden" name="encrypted" value="-----BEGIN PKCS7----------END PKCS7-----
                    ">
                    <input type="image" src="https://www.paypalobjects.com/pt_BR/i/btn/btn_cart_LG.gif" border="0" name="submit" alt="PayPal - A maneira fácil e segura de enviar pagamentos online!">
                    <img alt="" border="0" src="https://www.paypalobjects.com/pt_BR/i/scr/pixel.gif" width="1" height="1">
                  </form>
                </b-col>
              </b-row>
            </b-container>
        </b-modal>

    </b-container>
  </div>
</template>

<script>
export default {
  name: 'GifstList',
  components: {},
  data() {
    return {};
  },
  methods: {
    showModal(modal) {
      this.$refs[modal].show();
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';
.img {
  width: 200px;
}
.item {
  font-weight: bold;
}
.price {
  font-weight: bold;
  font-size: 1.5rem;
}
.card {
  cursor: pointer;
  height: 29rem;
  @include media-breakpoint-down(md) {
    height: 15rem !important;
  }
  box-shadow: 1px 4px 8px #ccc;
  p {
    font-weight: 700;
    font-size: 1rem;
  }
}
.block {
  margin-top: 2rem;
}
.card-img {
  padding: 5rem;
  align-self: center;
  @include media-breakpoint-down(md) {
    padding: 1rem;
    width: 10rem;
    height: 10rem;
  }
}
</style>




